"use client";

import React, { useState, useEffect } from "react";
import {
  Input,
  Button,
  Select,
  SelectItem,
  Card,
  useToast,
} from "@heroui/react";
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetFooter,
  SheetTitle,
  SheetDescription,
  SheetClose,
  SheetTrigger,
} from "@/components/ui/sheet";
import Image from "next/image";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";

interface DependantType {
  id: string | number;
  relationship: string;
  title: string;
  firstName: string;
  middleName: string;
  surname: string;
  idtypes: string;
  idnos: string;
  dob: string;
  gendere: string;
}

export interface FormdataType {
  policyScheme: string;
  relationship: string;
  staffId: string;
  title: string;
  firstname: string;
  middleName: string;
  lastname: string;
  idtype: string;
  idno: string;
  dateofbirth: string;
  gender: string;
  country: string;
  city: string;
  address: string;
  mobileno: string;
  eimail: string;
  dependantsData: DependantType[];
}

export interface ErrorsType {
  [key: string]: string;
}

const StaffMedicalForm: React.FC = () => {
  const { toast } = useToast();
  const today = new Date().toISOString().split("T")[0];

  const [formData, setFormData] = useState<FormdataType>({
    policyScheme: "Birdview Microinsurance Medical Scheme",
    relationship: "Principal",
    staffId: "",
    title: "",
    firstname: "",
    middleName: "",
    lastname: "",
    idtype: "",
    idno: "",
    dateofbirth: "",
    gender: "",
    country: "Kenya",
    city: "",
    address: "",
    mobileno: "",
    eimail: "",
    dependantsData: [
      {
        id: 1,
        relationship: "",
        title: "",
        firstName: "",
        middleName: "",
        surname: "",
        idtypes: "",
        idnos: "",
        dob: "",
        gendere: "",
      },
    ],
  });

  const [loaderIcon, setLoaderIcon] = useState(false);
  const [dependentCount, setDependentCount] = useState(1);
  const [errors, setErrors] = useState<ErrorsType>({});
  const [currentDependant, setCurrentDependant] = useState<DependantType | null>(null);
  const [openSheet, setOpenSheet] = useState(false);

  // Sync dependants count
  useEffect(() => {
    setFormData((prev) => {
      const newDependants = Array.from({ length: dependentCount }, (_, idx) => {
        return prev.dependantsData[idx] || {
          id: idx + 1,
          relationship: "",
          title: "",
          firstName: "",
          middleName: "",
          surname: "",
          idtypes: "",
          idnos: "",
          dob: "",
          gendere: "",
        };
      });
      return { ...prev, dependantsData: newDependants };
    });
  }, [dependentCount]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handlePhoneChange = (value: string) => {
    setFormData((prev) => ({ ...prev, mobileno: value }));
  };

  const handleAddDependant = () => {
    setDependentCount((prev) => prev + 1);
  };

  const handleOpenSheet = (dep: DependantType) => {
    setCurrentDependant(dep);
    setOpenSheet(true);
  };

  const handleCloseSheet = () => {
    setCurrentDependant(null);
    setErrors({});
    setOpenSheet(false);
  };

  const handleChangeDep = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setCurrentDependant((prev) => prev && { ...prev, [name]: value });
    setErrors((prev) => ({ ...prev, [name]: "" }));
  };

  const handleSaveDependant = () => {
    if (!currentDependant) return;
    const newErrors: ErrorsType = {};
    ["relationship", "title", "firstName", "middleName", "surname", "idtypes", "idnos", "dob", "gendere"].forEach(
      (key) => {
        if (!(currentDependant as any)[key]) newErrors[key] = `${key} is required`;
      }
    );
    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }
    setFormData((prev) => ({
      ...prev,
      dependantsData: prev.dependantsData.map((d) =>
        d.id === currentDependant.id ? currentDependant : d
      ),
    }));
    handleCloseSheet();
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoaderIcon(true);
    try {
      const res = await fetch("/api/medicalStaff_form", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });
      const data = await res.json();
      if (res.ok) {
        toast({ title: "Success", description: data.message, variant: "success" });
        handleReset();
      } else {
        toast({ title: "Error", description: data.error, variant: "destructive" });
      }
    } catch (err: any) {
      toast({ title: "Error", description: err.message, variant: "destructive" });
    } finally {
      setLoaderIcon(false);
    }
  };

  const handleReset = () => {
    setFormData({
      policyScheme: "Birdview Microinsurance Medical Scheme",
      relationship: "Principal",
      staffId: "",
      title: "",
      firstname: "",
      middleName: "",
      lastname: "",
      idtype: "",
      idno: "",
      dateofbirth: "",
      gender: "",
      country: "Kenya",
      city: "",
      address: "",
      mobileno: "",
      eimail: "",
      dependantsData: [
        {
          id: 1,
          relationship: "",
          title: "",
          firstName: "",
          middleName: "",
          surname: "",
          idtypes: "",
          idnos: "",
          dob: "",
          gendere: "",
        },
      ],
    });
    setDependentCount(1);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100 px-4">
      {loaderIcon && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-white/70">
          <div className="loaderIcon">Loading...</div>
        </div>
      )}

      <Card className="w-full max-w-3xl shadow-xl rounded-2xl p-6">
        <div className="flex justify-center my-4">
          <Image src="/images/logo.jpeg" alt="Logo" width={180} height={50} />
        </div>
        <h2 className="text-xl font-bold text-center mb-6">Staff Medical Detail Forms</h2>

        <form onSubmit={handleSubmit} className="space-y-4">
          <Input label="Policy Scheme" value={formData.policyScheme} disabled />
          <Input label="Relationship" value={formData.relationship} disabled />
          <Input label="Staff ID" name="staffId" value={formData.staffId} onChange={handleChange} />

          <Select label="Title" name="title" value={formData.title} onValueChange={(v) => handleChange({ target: { name: "title", value: v } } as any)}>
            {["Mr", "Mrs", "Miss", "Ms", "Dr", "Prof"].map((t) => (
              <SelectItem key={t} value={t}>{t}</SelectItem>
            ))}
          </Select>

          <Input label="First Name" name="firstname" value={formData.firstname} onChange={handleChange} />
          <Input label="Middle Name" name="middleName" value={formData.middleName} onChange={handleChange} />
          <Input label="Last Name" name="lastname" value={formData.lastname} onChange={handleChange} />

          <Select label="ID Type" name="idtype" value={formData.idtype} onValueChange={(v) => handleChange({ target: { name: "idtype", value: v } } as any)}>
            {["National ID", "Passport", "Birth Certificate"].map((t) => (
              <SelectItem key={t} value={t}>{t}</SelectItem>
            ))}
          </Select>

          <Input label="ID Number" name="idno" value={formData.idno} onChange={handleChange} />
          <Input type="date" label="Date of Birth" name="dateofbirth" value={formData.dateofbirth} onChange={handleChange} max={today} />

          <Select label="Gender" name="gender" value={formData.gender} onValueChange={(v) => handleChange({ target: { name: "gender", value: v } } as any)}>
            {["Male", "Female", "Others"].map((g) => (
              <SelectItem key={g} value={g}>{g}</SelectItem>
            ))}
          </Select>

          <Input label="Country" value={formData.country} disabled />
          <Input label="City" name="city" value={formData.city} onChange={handleChange} />
          <Input label="Address" name="address" value={formData.address} onChange={handleChange} />
          <PhoneInput country="ke" value={formData.mobileno} onChange={handlePhoneChange} inputStyle={{ width: "100%" }} />
          <Input label="Email" name="eimail" value={formData.eimail} onChange={handleChange} />

          <div className="flex justify-between items-center mt-4">
            <h3 className="font-semibold text-lg">Dependant Details</h3>
            <Button type="button" onClick={handleAddDependant}>Add Dependant</Button>
          </div>

          {formData.dependantsData.map((dep) => (
            <Button type="button" key={dep.id} onClick={() => handleOpenSheet(dep)} variant="outline" className="my-2">
              Edit Dependant {dep.id}
            </Button>
          ))}

          <div className="flex space-x-4 mt-6">
            <Button type="submit">Submit</Button>
            <Button type="button" color="destructive" onClick={handleReset}>Reset</Button>
          </div>
        </form>
      </Card>

      {/* Dependant Sheet */}
      <Sheet open={openSheet} onOpenChange={setOpenSheet}>
        <SheetContent side="bottom" className="max-w-lg rounded-t-xl p-6">
          <SheetHeader>
            <SheetTitle>Edit Dependant</SheetTitle>
          </SheetHeader>

          {currentDependant && (
            <div className="space-y-3">
              <Input label="Relationship" name="relationship" value={currentDependant.relationship} onChange={handleChangeDep} error={errors.relationship} />
              <Select label="Title" name="title" value={currentDependant.title} onValueChange={(v) => handleChangeDep({ target: { name: "title", value: v } } as any)}>
                {["Mr", "Mrs", "Miss", "Dr", "Prof", "Ms", "Master"].map((t) => (
                  <SelectItem key={t} value={t}>{t}</SelectItem>
                ))}
              </Select>
              <Input label="First Name" name="firstName" value={currentDependant.firstName} onChange={handleChangeDep} error={errors.firstName} />
              <Input label="Middle Name" name="middleName" value={currentDependant.middleName} onChange={handleChangeDep} error={errors.middleName} />
              <Input label="Surname" name="surname" value={currentDependant.surname} onChange={handleChangeDep} error={errors.surname} />
              <Select label="ID Type" name="idtypes" value={currentDependant.idtypes} onValueChange={(v) => handleChangeDep({ target: { name: "idtypes", value: v } } as any)}>
                {["National ID", "Passport", "Birth Certificate"].map((t) => (
                  <SelectItem key={t} value={t}>{t}</SelectItem>
                ))}
              </Select>
              <Input label="ID Number" name="idnos" value={currentDependant.idnos} onChange={handleChangeDep} error={errors.idnos} />
              <Input label="DOB" name="dob" type="date" value={currentDependant.dob} onChange={handleChangeDep} max={today} error={errors.dob} />
              <Select label="Gender" name="gendere" value={currentDependant.gendere} onValueChange={(v) => handleChangeDep({ target: { name: "gendere", value: v } } as any)}>
                {["Male", "Female"].map((g) => (
                  <SelectItem key={g} value={g}>{g}</SelectItem>
                ))}
              </Select>
            </div>
          )}

          <SheetFooter className="flex justify-end space-x-4 mt-4">
            <SheetClose asChild>
              <Button variant="outline">Cancel</Button>
            </SheetClose>
            <Button onClick={handleSaveDependant}>Save</Button>
          </SheetFooter>
        </SheetContent>
      </Sheet>
    </div>
  );
};

export default StaffMedicalForm;
